* {
    margin: 0;
}

body {
    font-family: "Zen Dots", cursive;
}

.fullApp {
    display: block;
    width: 100%;
}

.mainContainer {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    width: 800px;
    height: 350px;
    margin: 50px auto;
    background-color: #ffa600;
    border: 2px solid #000000;
    border-radius: 10px;
}

.title {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: nowrap;
            flex-wrap: nowrap;
    width: 100%;
    height: 20%;
    border-bottom: 1px solid #000000;
}

.title h1 {
    display: block;
    font-size: 30px;
    margin-left: 20px;
    margin-right: 340px;
}

.title h2 {
    display: block;
    font-size: 15px;
    margin-top: 2em;
}

.playerSection {
    width: 20%;
    height: 80%;
    border-right: 1px solid #000000;
}

.bpmSection,
.thirdLine {
    display: -webkit-flex;
    display: flex;
}

.playerSection label {
    display: block;
    margin-top: 45px;
    margin-left: 5px;
    margin-right: 15px;
}

#bpmSel,
#lengthSel {
    display: block;
    height: 20px;
    margin: 40px auto;
    background-color: #3d3d3d;
    color: #f0d800;
    border: 2px solid #000000;
}

.tripletButton {
    display: block;
    height: 20px;
    font-size: 15px;
    color: #000000;
    background-color: #807f7f;
    border: 2px solid #000000;
    border-radius: 3px;
    position: relative;
    top: -35px;
    left: 77px;
}

#tripletActive {
    color: #ff0000;
}

.thirdLine {
    margin-top: -60px;
    margin-bottom: -45px;
}

.thirdLine label {
    margin-right: 22px;
}

#lengthSel {
    width: 67px;
}

.playLine {
    display: -webkit-flex;
    display: flex;
    width: 100%;
    position: relative;
    top: 95px;
}

.playStopButton {
    display: inline-block;
    padding: 5px 10px;
    margin: auto;
    border: 2px solid #000000;
    border-radius: 5px;
    color: #000000;
    background-color: #807f7f;
}

.playButton {
    font-size: 30px;
}

#playButtonActive,
.stopButton:active {
    color: #ff0000;
}

#playWritten,
#stopWritten {
    display: block;
    margin: auto;
}

.stopButton {
    font-size: 20px;
    padding: 10px 15px;
}

.sampleSection {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    width: 80%;
    height: 60%;
    position: relative;
    left: 20%;
    bottom: 80%;
}

.singleSample {
    display: block;
    width: 9.8%;
    height: 100%;
    border-right: 1px solid #000000;
    text-align: center;
}

.singleSample h1 {
    display: block;
    font-size: 12px;
}

.singleSample label {
    font-size: 10px;
}

.singleSample input {
    font-size: 10px;
    color: #ffa600;
    background-color: #000000;
    border: 2px solid #000000;
    border-radius: 5px;
}

.listenIcon {
    display: block;
    margin: auto;
}

.listenButton {
    margin-top: 20px;
    background-color: #807f7f;
    color: #000000;
    border: 2px solid #000000;
    border-radius: 4px;
}

.selectorOff {
    display: block;
    margin: 20px auto;
    width: 8px;
    height: 8px;
    border: 2px solid #000000;
    border-radius: 8px;
    background-color: #4e0000;
}

.selectorOn {
    display: block;
    margin: 20px auto;
    width: 8px;
    height: 8px;
    border: 2px solid #000000;
    border-radius: 8px;
    background-color: #ff0000;
}

.selectButton {
    background-color: #807f7f;
    color: #000000;
    border: 2px solid #000000;
    border-radius: 4px;
}

#tomSection {
    border-right: none;
}

.sequencerSection {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    width: 80.1%;
    height: 20%;
    position: relative;
    left: 20%;
    bottom: 80%;
    background-color: #2c2c2c;
    border-bottom-right-radius: 10px;
}

.singleButtonFirst {
    display: block;
    width: 6.2%;
}

.firstLineSpyOff,
.secondLineSpyOff,
.thirdLineSpyOff {
    display: block;
    width: 6px;
    height: 6px;
    border: 2px solid #000000;
    border-radius: 6px;
    background-color: #4e0000;
    margin: 3px auto;
}

.firstLineSpyOn,
.secondLineSpyOn,
.thirdLineSpyOn {
    display: block;
    width: 6px;
    height: 6px;
    border: 2px solid #000000;
    border-radius: 6px;
    background-color: #ff0000;
    margin: 3px auto;
}

.sequencerButton {
    display: block;
    margin: auto;
    width: 60%;
    height: 50%;
    background-color: #807f7f;
    border: 2px solid #000000;
    border-radius: 4px;
}

.button4 {
    background-color: #2c2c2c;
}

.button3 {
    background-color: #7a6666;
}
